body {
    margin: 8px;
    font-family: 'Open Sans', sans-serif;
}

.luxbar *, .luxbar *:before, .luxbar *:after {
  box-sizing: border-box; }

.luxbar {
  background-color: #006064;
  color: #fff;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  height: 3.5em;
  position: relative;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%; }

.luxbar-shadow1 {
  -webkit-filter: drop-shadow(0 2px 2px #424242);
          filter: drop-shadow(0 2px 2px #424242); }

#luxbar-checkbox {
  display: none; }

.luxbar-hamburger {
  display: none; }

.luxbar a {
  text-decoration: none;
  color: inherit; }

.luxbar-brand {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 1.5rem;
  font-size: 1.2rem;
  background-color: inherit;
  flex: 1 1; }

.luxbar-items {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  height: 3.5em;
  background-color: #006064; }

.luxbar-items li {
  height: 100%; }

.luxbar-items li a {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  padding: 0 1.5rem;
  -webkit-transition: background-color 0.2s ease;
  transition: background-color 0.2s ease; }

.luxbar-items li a:hover, .luxbar-items li a.luxbar-active {
  background-color: #00838f; }

@media (max-width: 900px) {
  .luxbar-hamburger {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 3.5em;
    background-color: inherit;
    height: 3.5em; }
  .luxbar-hamburger span, .luxbar-hamburger span::before, .luxbar-hamburger span::after {
    display: block;
    height: 2px;
    width: 1.5rem;
    border-radius: 2px;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease; }
  #luxbar-checkbox:checked ~ .luxbar-hamburger span {
    background-color: transparent; }
  #luxbar-checkbox:checked ~ .luxbar-hamburger span::before, #luxbar-checkbox:checked ~ .luxbar-hamburger span::after {
    margin-top: 0; }
  .luxbar-hamburger span::before, .luxbar-hamburger span::after {
    content: '';
    position: absolute; }
  .luxbar-hamburger span::before {
    margin-top: -0.5rem; }
  .luxbar-hamburger span::after {
    margin-top: 0.5rem; }
  #luxbar-checkbox:checked ~ .luxbar-hamburger-doublespin span::before {
    -webkit-transform: rotate(225deg);
            transform: rotate(225deg); }
  #luxbar-checkbox:checked ~ .luxbar-hamburger-doublespin span::after {
    -webkit-transform: rotate(-225deg);
            transform: rotate(-225deg); }
  #luxbar-checkbox:checked ~ .luxbar-hamburger-spin span::before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); }
  #luxbar-checkbox:checked ~ .luxbar-hamburger-spin span::after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg); }
  .luxbar-hamburger span,
  .luxbar-hamburger span::before,
  .luxbar-hamburger span::after {
    background-color: #fff; }
  .luxbar-items {
    flex-direction: column;
    width: 100%;
    height: auto;
    z-index: -1;
    position: relative; }
  .luxbar-items::before {
    background-color: red;
    content: " ";
    display: block;
    position: absolute;
    top: 0;
    height: 100%;
    bottom: 100%;
    width: 100px; }
  .luxbar-items > li {
    position: absolute;
    background-color: inherit;
    height: auto;
    top: -3.5em;
    -webkit-transition: top 0.35s ease-in-out;
    transition: top 0.35s ease-in-out;
    width: 100%; }
  .luxbar-items > li a {
    background-color: inherit;
    height: 3.5em; }
  .luxbar-items > li:nth-child(1) {
    z-index: -1; }
  .luxbar-items > li:nth-child(2) {
    z-index: -2; }
  .luxbar-items > li:nth-child(3) {
    z-index: -3; }
  .luxbar-items > li:nth-child(4) {
    z-index: -4; }
  #luxbar-checkbox:checked ~ .luxbar-items > li:nth-child(1) {
    top: 0; }
  #luxbar-checkbox:checked ~ .luxbar-items > li:nth-child(2) {
    top: 3.5em; }
  #luxbar-checkbox:checked ~ .luxbar-items > li:nth-child(3) {
    top: 7em; }
  #luxbar-checkbox:checked ~ .luxbar-items > li:nth-child(4) {
    top: 10.5em; }
  #luxbar-checkbox:checked ~ .luxbar-items > li:nth-child(5) {
    top: 14em; } }

